*, *::after, *::before {
  margin: 0px;
  padding: 0px;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

button {
  outline: none;
  border: none;
  background-color: transparent;
}

main {
  margin-top: 2.4rem;
}

body {
  font-weight: 400;
  line-height: 1.5;
}

.ticker__stock__element__text {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: inherit;
}

.enterprise__title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #29990B;
}
.enterprise-paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  color: #434343;
}
.enterprise_news__title {
  font-family: "Merriweather", serif;
  font-weight: 700;
  font-size: 3.2rem;
  color: #000;
}
@media screen and (max-width: 760px) {
  .enterprise_news__title {
    font-size: 2.3rem;
  }
}
.enterprise-editor-box__title {
  font-family: "Merriweather", serif;
  font-weight: 700;
  font-size: 1.4rem;
  color: #000;
}
.enterprise-industry__title {
  font-family: "Merriweather", serif;
  font-weight: 700;
  font-size: 1.8rem;
  color: #fff;
}

.u-center-text {
  text-align: center;
}

.u-margin-bottom-big {
  margin-bottom: 8rem;
}

.u-margin-bottom-medium {
  margin-bottom: 4rem;
}

.u-margin-bottom-small {
  margin-bottom: 1.5rem;
}

.u-text-transform-upper {
  text-transform: uppercase;
}

.u-dark-light {
  color: rgba(0, 0, 0, 0.7) !important;
}

.u-icon-img-small {
  width: 1.4rem;
  height: 1.4rem;
}

.u-icon-img-medium {
  width: 1.6rem;
  height: 1.6rem;
}

.u-icon-img-large {
  width: 2rem;
  height: 2rem;
}

.u-w-full {
  width: 100%;
}

.u-font-family-ibm-plex {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-weight: 700;
}

.u-font-family-roboto {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.u-font-weigh-700 {
  font-weight: 700;
}

.u-sm-font-size {
  font-size: 1.2rem;
}

.u-md-font-size {
  font-size: 1.4rem;
}

.u-lg-font-size {
  font-size: 1.6rem !important;
}

.u-xl-font-size {
  font-size: 3.2rem;
}

.u-container {
  width: 90%;
  margin: 0 auto;
}
@media screen and (max-width: 760px) {
  .u-container {
    width: 95%;
  }
}

.u-mt-2 {
  margin-top: 0.2rem;
}

.u-my-8 {
  margin: 0.8rem 0rem;
}

.u-mt-9 {
  margin-top: 0.9rem;
}

.u-mt-10 {
  margin-top: 1rem;
}

.u-mb-24 {
  margin-bottom: 2.4rem;
}

.u-position-relative {
  position: relative;
}

.u-m-10 {
  margin: 1rem;
}

.w-30 {
  width: 30%;
}

.color-light-dark {
  color: #434343;
}

@media screen and (max-width: 760px) {
  .u-m-none {
    display: none !important;
  }
}

.btn {
  padding: 0.8rem 1.6rem;
  border-radius: 0.2rem;
  border: none;
  outline: none;
}
.btn--primary {
  background-color: #29990B;
  color: #fff;
}
.btn--medium {
  font-size: 1.4rem;
}
.btn--large {
  font-size: 1.6rem;
}
.btn-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  gap: 0.4rem;
}
.btn--card {
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.btn--white {
  color: #000;
  background-color: #fff;
}

.ticker {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  color: #fff;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), #29990b;
  padding: 0 8rem;
}
@media screen and (max-width: 450px) {
  .ticker {
    overflow-x: scroll;
    padding: 0 3rem;
  }
}
.ticker__stock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: "row";
  width: 100%;
}
.ticker__stock__element {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  padding: 0.6rem 0;
}
@media screen and (max-width: 450px) {
  .ticker__stock__element span {
    white-space: nowrap;
  }
}
.ticker__stock__element-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  padding: 0 0.8rem;
  gap: 0.4rem;
}
.ticker__stock__element__text--up {
  color: #00E277;
}
.ticker__stock__element__text--neutral {
  color: #8D8D8D;
}
.ticker__stock__element__text--down {
  color: #FF4B5F;
}

.sperator {
  width: 1px;
  height: 16px;
  background-color: #8D8D8D;
}
.sperator-dashed {
  border: 1px dashed rgba(0, 0, 0, 0.2);
}
.sperator-horizontal {
  height: 1px;
  border: 1px dashed rgba(0, 0, 0, 0.2);
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: "row";
  color: #000;
  font-size: 1.4rem;
  padding: 0 4.9rem;
}
@media screen and (max-width: 760px) {
  .navbar {
    display: none;
  }
}
.navbar-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: "row";
  box-shadow: 0px -1px 0px rgba(0, 0, 0, 0.2), 0px 1px 0px rgba(0, 0, 0, 0.2);
  gap: 1rem;
  width: 96%;
}
.navbar__item {
  display: inline-block;
  padding: 1.6rem 1.2rem;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  text-decoration: none;
  color: inherit;
}

.fieldset {
  border: 0.2rem solid #29990B;
  border-left-width: 0px;
  border-right-width: 0px;
  border-bottom-width: 0px;
}
.fieldset__legend {
  margin: 0 auto;
  color: #000;
  padding: 0rem 2rem;
}

.card {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 2rem;
  gap: 1rem;
  background: #ffffff;
  border: 0.75px solid rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}
@media screen and (max-width: 760px) {
  .card {
    max-width: 95%;
  }
}
.card__title {
  line-height: 23px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  font-size: 1.8rem;
  color: #000;
  padding-top: 1.3rem;
}
.card__title__bold {
  font-weight: 700;
}
.card-paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  color: #434343;
}
.card--fixed-height {
  min-height: 197.81px;
  max-height: 197.81px;
}
.card--green {
  box-shadow: inset 0px 4px 0px #29990B;
}
.card--light-green {
  box-shadow: inset 0px 4px 0px #94CC85;
}
.card--orange {
  box-shadow: inset 0px 4px 0px #FF7801;
}
.card--grey {
  box-shadow: inset 0px 4px 0px #555555;
}
.card--yellow {
  box-shadow: inset 0px 4px 0px #F7A41A;
}

.slider {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: "row";
  overflow-x: hidden;
  gap: 1rem;
  position: relative;
}
@media screen and (max-width: 760px) {
  .slider {
    flex-wrap: wrap;
  }
}
.slider__item {
  min-width: 24.2%;
  text-align: start;
  transition: transform 0.2s;
}
.slider__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  padding: 1rem;
  position: absolute;
  width: 3.2rem;
  height: 3.2rem;
  background-color: #000 !important;
  border-radius: 2px;
}
.slider__btn--left {
  left: -48px;
  top: calc(50% - 16px + 0.09px);
}
@media screen and (max-width: 760px) {
  .slider__btn--left {
    display: none;
  }
}
.slider__btn--right {
  right: -48px;
  top: calc(50% - 16px + 0.09px);
}
@media screen and (max-width: 760px) {
  .slider__btn--right {
    display: none;
  }
}

.subscription-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
}
.subscription-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  padding: 3rem 0;
  gap: 1rem;
  color: #fff;
  background-color: #29990B;
  border-radius: 0.2rem;
  box-shadow: 0rem 0.8rem 0.8rem -0.4rem rgba(0, 0, 0, 0.1);
}
@media screen and (max-width: 760px) {
  .subscription-box {
    flex-wrap: wrap;
    width: 95%;
  }
}
.subscription-box__title {
  font-family: "Merriweather", serif;
  font-weight: 700;
  font-size: 2.4rem;
}
.subscription-box__title--bold {
  font-weight: 900;
}
.subscription-box__paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  width: 55%;
}
@media screen and (max-width: 760px) {
  .subscription-box__paragraph {
    width: 95%;
  }
}

.header__partial {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: "row";
  padding: 2rem 3rem 1rem;
  gap: 1rem;
}
.header__partial__edition {
  font-family: "Roboto", sans-serif;
  font-size: 1.2rem;
  color: #8D8D8D;
  gap: 0.4px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: "row";
}
.header-logo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: "row";
  padding: 0rem 0rem 2rem 0rem;
  width: 90%;
  margin: 0 auto;
  gap: 1rem;
  font-size: 1.4rem;
  color: #000;
}
.header-logo-container__actions {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: "row";
  gap: 1.6rem;
}
@media screen and (max-width: 760px) {
  .header-logo-container__actions {
    display: none;
  }
}
.header-logo-container__box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: "row";
  padding: 0px;
  gap: 0.4rem;
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-weight: 700;
}
@media screen and (max-width: 760px) {
  .header-logo-container__box {
    display: none;
  }
}
.header-logo-container__buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: "row";
  gap: 1.6rem;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 2.4rem;
}
.footer-enterprise {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding: 4rem 0rem 0rem;
  gap: 2rem;
  border-radius: 2rem;
}
@media screen and (max-width: 760px) {
  .footer-enterprise {
    flex-wrap: wrap;
  }
}
.footer-enterprise-box {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: "row";
  padding: 0rem 0rem 2rem;
  gap: 2rem;
}
@media screen and (max-width: 760px) {
  .footer-enterprise-box {
    flex-wrap: wrap;
  }
}
.footer-enterprise-info {
  flex-basis: 50%;
}
@media screen and (max-width: 760px) {
  .footer-enterprise-info {
    flex-basis: 100%;
  }
}
.footer-enterprise-info__paragrapgh {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  color: #434343;
}
.footer-enterprise-data {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  flex-grow: 1;
  gap: 1rem;
}
.footer-enterprise-data__title {
  color: #29990B;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
}
.footer-enterprise-data__link {
  color: #000;
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-weight: 700;
  font-size: 1.4rem;
  text-decoration: none;
}
.footer-copy-rights {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  padding: 2rem 0rem 4rem;
  gap: 0.9rem;
}
.footer-copy-rights__copyright {
  color: #000;
}
.footer-copy-rights__paragrapgh {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  color: #8D8D8D;
}
@media screen and (max-width: 760px) {
  .footer-copy-rights__paragrapgh {
    width: 95%;
  }
}
.footer-copy-rights a {
  color: #29990B;
  text-decoration: underline;
}

.enterprise {
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  flex-direction: "row";
  gap: 2rem;
}
@media screen and (max-width: 760px) {
  .enterprise {
    flex-wrap: wrap;
  }
}
.enterprise-box {
  flex-basis: 70%;
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  flex-direction: "row";
  gap: 2rem;
  border-style: dashed;
  border-left-width: 1px;
  border-right-width: 1px;
  border-top-width: 0;
  border-bottom-width: 0;
  border-color: rgba(0, 0, 0, 0.2);
  padding: 0rem 2rem;
}
@media screen and (max-width: 760px) {
  .enterprise-box {
    flex-basis: 100%;
    flex-wrap: wrap;
  }
}
.enterprise__am {
  flex-basis: 30%;
}
@media screen and (max-width: 760px) {
  .enterprise__am {
    flex-basis: 100%;
    order: 2;
  }
}
.enterprise__date {
  color: #8D8D8D;
}
.enterprise__news {
  flex-basis: 70%;
}
@media screen and (max-width: 760px) {
  .enterprise__news {
    flex-basis: 100%;
    order: 1;
  }
}
.enterprise-editor-box {
  flex-basis: 30%;
}
@media screen and (max-width: 760px) {
  .enterprise-editor-box {
    flex-basis: 100%;
  }
}
.enterprise-editor-box__sub-news {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: "row";
  gap: 2rem;
}
.enterprise-editor-box__sub-news div {
  flex: 1;
}
.enterprise-industry {
  margin: 2.4rem 0;
}
.enterprise-industry__title {
  display: inline-block;
  padding: 0.8rem 1.6rem;
  margin: 0 auto;
  background-color: #000;
  border-radius: 2px;
}